html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button,
input {
  font: inherit;
}

body {
  font-family: "Chakra Petch", sans-serif;
  font-size: 16px;
  font-weight: 400;
  box-sizing: border-box;
  height: 100dvh;
}

.wrapper {
  box-sizing: border-box;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.header,
.footer {
  flex-grow: 0;
  flex-shrink: 0;
}

.main {
  flex-grow: 1;
}

.header {
  padding-top: 20px;
}
.header__logo {
  display: flex;
  align-items: flex-end;
  margin: 0 auto;
  width: fit-content;
}
.header__logo-text {
  font-weight: 600;
}

body {
  background: #5ecf4a;
}

.header {
  text-align: center;
  padding-top: 15px;
}

.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer {
  text-align: center;
}

.form-wrapper {
  max-width: 310px;
  width: 100%;
}

.registration-form {
  display: flex;
  flex-direction: column;
}
.registration-form__title {
  font-size: 38px;
  font-weight: 600;
  margin-bottom: 16px;
}
.registration-form__input {
  padding: 12px;
  font-size: 18px;
  background-color: transparent;
  border: 2px solid #0f553b;
  border-radius: 8px;
  margin-bottom: 20px;
}
.registration-form__input::placeholder {
  color: rgba(0, 0, 0, 0.3);
  font-weight: 600;
}
.registration-form__button {
  cursor: pointer;
  width: 50%;
  margin: 0 auto;
  padding: 10px;
  border-radius: 8px;
  border: none;
  background-color: #0c9746;
  font-size: 18px;
  font-weight: 600;
  color: #fff;
}
.registration-form__button:active {
  background-color: #0eaf51;
}