@import './base/reset';
@import './base/base';
@import './base/vars';

@import './blocks/header-logo';

body {
    background: $color-green-4;
}

.header {
    text-align: center;
    padding-top: 15px;
}

.main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.footer {
    text-align: center;
}

.form-wrapper {
    max-width: 310px;
    width: 100%;
}

.registration-form {
    display: flex;
    flex-direction: column;

    &__title {
        font-size: 38px;
        font-weight: 600;
        margin-bottom: 16px;
    }

    &__input {
        padding: 12px;
        font-size: 18px;
        background-color: transparent;
        border: 2px solid $color-green-1;
        border-radius: 8px;
        margin-bottom: 20px;

        &::placeholder {
            color: rgba(0,0,0, 0.3);
            font-weight: 600;
        }
    }

    &__button {
        cursor: pointer;
        width: 50%;
        margin: 0 auto;
        padding: 10px;
        border-radius: 8px;
        border: none;
        background-color: $color-green-3;
        font-size: 18px;
        font-weight: 600;
        color: $color-white;

        &:active {
            background-color: lighten($color-green-3, 5%);
        }
    }

}